import styled from "styled-components";

export const NavbarCon = styled.div`
  padding: 25px 0;
  margin-top: 1px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media screen and (max-width: 500px) {
    flex-direction: column;
  }
`;
export const LogoIcon = styled.img`
  width: 60px;
  border-radius: 15px;
`;
export const LogoCon = styled.div`
  display: flex;
  align-items: center;
  cursor: pointer;
`;

export const LogoName = styled.a`
  font-family: Fredoka One;
  font-style: normal;
  font-weight: normal;
  font-size: 25px;
  line-height: 18px;
  color: #ffffff;
  margin-left: 10px;
  text-decoration: none;
`;

export const NavLinkCon = styled.div`
  width: 600px;
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media screen and (max-width: 500px) {
    justify-content: space-evenly;
    width: 220px;
    margin-top: 10px;
  }
`;
