import React from "react";
import { AboutCon, AboutItem, AboutMe, ImgMe } from "./style";
import Me1 from "./rasmlar/me1.jpg";
import Me2 from "./rasmlar/me2.jpg";
import Me3 from "./rasmlar/me3.jpg";
import Fade from "react-reveal/Fade";
import Shake from "react-reveal/Shake";
import { NavLink } from "react-router-dom";

const About = () => {
  return (
    <AboutCon>
      <Shake>
        <AboutItem>About me</AboutItem>
      </Shake>
      <AboutMe>
        <Fade cascade left>
          <ImgMe src={Me1} />
        </Fade>
        <Fade cascade right>
          <div>
            <h1>Assalamu alaykum once again.</h1>
            <br />
            <br />
            <h2>
              My name is Abdullah, my surname is Kiyomov, my nationality is
              Uzbek, I am 17 years old now - 2021. My profession is Frontend
              Developer, or React Developer.
            </h2>
          </div>
        </Fade>
      </AboutMe>
      <AboutMe>
        <Fade cascade left>
          <div>
            <h1>About my skills</h1>
            <br />
            <br />
            <h2>Frontend Development:</h2>
            <br />
            <h2>
              I currently know HTML, CSS, SASS, JavaScript, ReactJS, Redux, GIT,
              Github, LINUX, Terminal. Then I start learning the knowledge
              again.
            </h2>
          </div>
        </Fade>
        <Fade cascade right>
          <ImgMe src={Me2} />
        </Fade>
      </AboutMe>
      <AboutMe>
        <Fade cascade left>
          <ImgMe src={Me3} />
        </Fade>
        <Fade cascade right>
          <div>
            <h1>Contact me</h1>
            <br />
            <h2>
              All my internet networks are left to contact me on the{" "}
              <NavLink to="/">" Home "</NavLink>
              page. Contact me and find out more about me! <br />
              Assalamu alekum wa rahmatullah
            </h2>
          </div>
        </Fade>
      </AboutMe>
    </AboutCon>
  );
};

export default About;
