import styled from "styled-components";

export const AboutCon = styled.div`
  margin-top: 55px;
  margin-bottom: 55px;
  display: flex;
  flex-direction: column;
`;
export const AboutMe = styled.div`
  padding: 0 100px 40px 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  div {
      display: flex;
      flex-direction: column;
      align-items: flex-flex-start;
      margin-left: 40px;
    h1 {
      font-family: Hammersmith One;
      font-size: 50px;
      color: orange;
    }
    h2{
        font-family: Fredoka One;
        font-size: 35px;
        color:white;
        font-weight: 100;
    }
  }
`;

export const ImgMe = styled.img`
  width: 400px;
  border-radius: 15px;
`;

export const AboutItem = styled.div`
  font-family: Hammersmith One;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  /* line-height: 54px; */
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  margin-top: -20px;
  margin-bottom: 30px;
`;