import styled from "styled-components";

export const InfoCon = styled.div`
  display: flex;
  margin-top: 50px;
  align-items: center;
  justify-content: space-between;
`;

export const Hello = styled.div`
  font-family: Hammersmith One;
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  line-height: 54px;
  display: flex;
  align-items: center;
  color: #e4e4e4;
  margin-bottom: 25px;
`;

export const Iam = styled.div`
  font-family: Hammersmith One;
  font-style: normal;
  font-weight: bold;
  font-size: 66px;
  line-height: 82px;
  display: flex;
  align-items: center;
  color: #e4e4e4;
  margin-bottom: 25px;
  @media screen and (max-width:500px){
    display: none;
  }
`;

export const Infotext = styled.div`
  display: flex;
  flex-direction: column;
`;

export const AboutInfo = styled.div`
  font-family: Hammersmith One;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 21px;
  display: flex;
  align-items: center;
  margin-bottom: 25px;
  /* text */

  color: #e4e4e4;
`;

