export const ContactMenu = styled.div`
  width: 50px;
  height: 50px;
  border: 3px solid black;
  border-radius: 50%;
  background-color: #464646;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all .1s linear;
  :hover{
    transform: scale(1.1);
    border: 3px solid orange;
    background-color: white;
  }
`;

export const LogoContact = styled.img`
  width: 30px;
`;

export const ContactBar = styled.div`
  display: flex;
  width: 500px;
  padding: 10px 5px;
  align-items: center;
  justify-content: space-around;
  border: 3px solid #262626;
  border-radius: 30px;
  background-color: #565656;
`;

export const LinkForIcons = styled.a``