import React from "react";
import PhotoCont from "./Photo";
import GithubLogo from "./rasm/githubIcon.png";
import FacebookLogo from "./rasm/facebookIcon.png";
import TelegramLogo from "./rasm/telegramIcon.png";
import LinkedinLogo from "./rasm/linkedinIcon.png";
import YoutubeLogo from "./rasm/youtubeIcon.png";
import InstagramLogo from "./rasm/instagramIcon.png";
import {
  AboutInfo,
  ContactBar,
  ContactMenu,
  Hello,
  Iam,
  InfoCon,
  Infotext,
  LinkForIcons,
  LogoContact,
} from "./style";
import Fade from "react-reveal/Fade";
import Shake from "react-reveal/Shake";
import Typical from "react-typical";

const Info = () => {
  return (
    <InfoCon>
      <Infotext>
        <Fade left cascade>
          <div>
            <Hello>Assalamu Alayka</Hello>
            <Hello>My name's Kiyomov Abdulloh</Hello>
            <Iam>
              I'm{"_"}
              <Typical
                loop={Infinity}
                steps={[
                  `Frontend Web Dev`,
                  1500,
                  `React Web Dev`,
                  1500,
                  `Blogger`,
                  1500,
                  `Programmer`,
                  1500,
                ]}
              ></Typical>
            </Iam>
            <AboutInfo>
              I've been doing React Apps, Front-End <br /> development for a
              year now. Do you <br /> need a website Apps or website, site
              layout, or maybe a <br />
              turnkey website? Then contact me.
            </AboutInfo>
          </div>
        </Fade>
        <Shake>
          {/* <ContactBtn target="_blank" href="https://t.me/kiyomov_dev">
            Contact me
          </ContactBtn> */}
          <ContactBar>
            <LinkForIcons href="https://t.me/kiyomov1" target="_blank">
              <ContactMenu>
                <LogoContact src={TelegramLogo} />
              </ContactMenu>
            </LinkForIcons>
            <LinkForIcons href="https://www.linkedin.com/in/abdulloh-kiyomov-462a01215/" target="_blank">
              <ContactMenu>
                <LogoContact src={LinkedinLogo} />
              </ContactMenu>
            </LinkForIcons>
            <LinkForIcons href="https://www.facebook.com/profile.php?id=100070284912371" target="_blank">
              <ContactMenu>
                <LogoContact src={FacebookLogo} />
              </ContactMenu>
            </LinkForIcons>
            <LinkForIcons href="https://github.com/q0abd" target="_blank">
              <ContactMenu>
                <LogoContact src={GithubLogo} />
              </ContactMenu>
            </LinkForIcons>
            <LinkForIcons href="https://www.instagram.com/kiyomoff1/" target="_blank">
              <ContactMenu>
                <LogoContact src={InstagramLogo} />
              </ContactMenu>
            </LinkForIcons>
            <LinkForIcons href="https://www.youtube.com/channel/UCB0Ryoxqhqu_h_FBEY233Xg" target="_blank">
              <ContactMenu>
                <LogoContact src={YoutubeLogo} />
              </ContactMenu>
            </LinkForIcons>
          </ContactBar>
        </Shake>
      </Infotext>
      <PhotoCont />
    </InfoCon>
  );
};

export default Info;
