import React from "react";
import Logo from "./logo.jpg";
import { LogoCon, LogoIcon, LogoName, NavbarCon, NavLinkCon } from "./style";
import Fade from "react-reveal/Fade";
import "./style.css";
import { NavLink } from "react-router-dom";

const Navbar = () => {
  return (
    <Fade top cascade>
      <NavbarCon>
        <LogoCon>
          <LogoIcon src={Logo} />
          <LogoName
            target="_blank"
            href="https://www.youtube.com/channel/UCB0Ryoxqhqu_h_FBEY233Xg"
          >
            Kiyomov Dev
          </LogoName>
        </LogoCon>
        <NavLinkCon>
          <NavLink to="/">
            <button class="custom-btn btn-16">Home</button>
          </NavLink>
          <NavLink to="/about">
            <button class="custom-btn btn-16">About</button>
          </NavLink>
          <NavLink to="/portfolio">
            <button class="custom-btn btn-16">Portfolio</button>
          </NavLink>
        </NavLinkCon>
      </NavbarCon>
    </Fade>
  );
};

export default Navbar;
