import React from "react";
import Pro1 from "./imges/pro1.jpg";
import Pro2 from "./imges/pro2.jpg";
import Pro3 from "./imges/pro3.jpg";
import Pro4 from "./imges/pro4.jpg";
import Pro5 from "./imges/pro5.jpg";
import Pro6 from "./imges/pro6.jpg";
import AliceCarousel from "react-alice-carousel";
import "react-alice-carousel/lib/alice-carousel.css";
import "./style.css";
import Fade from "react-reveal/Fade";
// import Shake from "react-reveal/Shake";
// import Typical from "react-typical";
import {
  // AboutPro,
  AboutProItem,
  AboutProItemInfo,
  Carousel,
  CarouselCon,
  CarouselItem,
  // CarRow,
  ContactBtn,
  ImgPro,
  PortfolioCon,
  PortoCon,
  PortoContainer,
} from "./style";

const handleDragStart = (e) => e.preventDefault();
const items = [
  <img width="790" src={Pro1} alt='rasmcha' onDragStart={handleDragStart} />,
  <img width="790" src={Pro4} alt='rasmcha' onDragStart={handleDragStart} />,
  <img width="790" src={Pro2} alt='rasmcha' onDragStart={handleDragStart} />,
  <img width="790" src={Pro5} alt='rasmcha' onDragStart={handleDragStart} />,
  <img width="790" src={Pro6} alt='rasmcha' onDragStart={handleDragStart} />,
  <img width="790" src={Pro3} alt='rasmcha' onDragStart={handleDragStart} />,
  // <img width="150" src={Pro3} onDragStart={handleDragStart} />,
];
const sk = `>>>`;
const skr = `<<<`;
export const Portfolio = () => {
  return (
    <CarouselCon>
      <CarouselItem id="sec-1">Portfolio</CarouselItem>
      <Fade top cascade>
        <Carousel>
          <AliceCarousel
            disableDotsControls
            animationType="slide"
            mouseTracking
            items={items}
          />
        </Carousel>
      </Fade>
      <Fade bottom cascade>
        {/* <Shake> */}
        <a href="#sec-2">
          <div class="scroll-down"></div>
        </a>
        {/* </Shake> */}
      </Fade>
      <PortoContainer id="sec-2">
        <Fade left cascade>
          <PortfolioCon>
            <PortoCon>
              <AboutProItem>' SkillCare ' website</AboutProItem>
              <AboutProItemInfo>
                SkillCare - this is the personal website of the <br /> online
                learning center.More about this site.
              </AboutProItemInfo>
              <ContactBtn
                href="https://skill-care-hazel.vercel.app/"
                target="_blank"
              >
                See more{` `}
                {sk}
              </ContactBtn>
            </PortoCon>
            <ImgPro src={Pro1} />
          </PortfolioCon>
        </Fade>
        <Fade right cascade>
          <PortfolioCon>
            <ImgPro src={Pro2} />
            <PortoCon>
              <AboutProItem>' Portfolio 2 ' website</AboutProItem>
              <AboutProItemInfo>
                Portfolio - this is my personal portfolio website.
              </AboutProItemInfo>
              <ContactBtn
                href="https://porfolio1-tau.vercel.app/"
                target="_blank"
              >
                {skr}
                {` `}See more
              </ContactBtn>
            </PortoCon>
          </PortfolioCon>
        </Fade>
        <Fade left cascade>
          <PortfolioCon>
            <PortoCon>
              <AboutProItem>' Locus ' website</AboutProItem>
              <AboutProItemInfo>
                Locus - online shopping market . In coming soon!
              </AboutProItemInfo>
              <ContactBtn href="" target="_blank">
                See more{` `}
                {sk}
              </ContactBtn>
            </PortoCon>
            <ImgPro src={Pro4} />
          </PortfolioCon>
        </Fade>
      </PortoContainer>
    </CarouselCon>
  );
};

export default Portfolio;
